}
body {
	/* background: url("./img/universe.jpg") no-repeat;
	background-position: center;
	background-size: cover; */
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

nav {
	position: fixed;
	display: -webkit-flex;
	display: flex;
	height: 65px;
	width: 100%;
	background: #6e00b8;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	padding: 0 50px 0 50px;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	box-shadow: 0.2px 5px 5px #707070;
	z-index: 2;
}
nav .logo img {
	width: 15rem;
	height: 4rem;
}
nav ul {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	list-style: none;
}
nav ul li {
	margin: 0 5px;
}
nav ul li a {
	color: #b3b3b3;
	text-decoration: none;
	font-size: 18px;
	font-weight: 500;
	padding: 8px 10px;
	border-radius: 5px;
	letter-spacing: 1px;
	transition: all 0.5s ease;
}
nav ul li a.active,
nav ul li a:hover {
	color: #111;
	background: #b3b3b3;
}
nav .menu-btn i {
	color: #b3b3b3;
	font-size: 22px;
	cursor: pointer;
	display: none;
}
input[type="checkbox"] {
	display: none;
}
@media (max-width: 1000px) {
	nav {
		padding: 0 40px 0 50px;
	}
}
@media (max-width: 920px) {
	nav .menu-btn i {
		display: block;
	}
	#click:checked ~ .menu-btn i:before {
		content: "\f100";
	}
	nav ul {
		position: fixed;
		top: 80px;
		left: -100%;
		background: #111;
		height: 100vh;
		width: 100%;
		text-align: center;
		display: block;
		transition: all 0.3s ease;
	}
	#click:checked ~ ul {
		left: 0;
	}
	nav ul li {
		width: 100%;
		margin: 40px 0;
	}
	nav ul li a {
		width: 100%;
		margin-left: -100%;
		display: block;
		font-size: 20px;
		transition: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
	}
	#click:checked ~ ul li a {
		margin-left: 0px;
	}
	nav ul li a.active,
	nav ul li a:hover {
		background: none;
		color: rgb(0, 211, 211);
	}
}

.active {
	background-color: #111;
	color: rgb(0, 150, 150);
}

