.page-content {
	display: grid;
	grid-gap: 2rem;
	padding: 1rem;
	max-width: 1500px;
	margin: 0 auto;
	font-family: "Rubik", sans-serif;
	font-family: var(--font-sans);
}
@media (min-width: 600px) {
	.page-content {
		grid-template-columns: repeat(2, 1fr);
	}
}
@media (min-width: 800px) {
	.page-content {
		grid-template-columns: repeat(4, 1fr);
	}
}

.about-card {
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: flex-end;
	        align-items: flex-end;
	overflow: hidden;
	padding: 1rem;
	width: 100%;
	text-align: left;
	color: whitesmoke;
	background-color: whitesmoke;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.1),
		0 4px 4px rgba(0, 0, 0, 0.1), 0 8px 8px rgba(0, 0, 0, 0.1),
		0 16px 16px rgba(0, 0, 0, 0.1);
}
@media (min-width: 600px) {
	.about-card {
		height: 350px;
	}
}
.about-card:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 110%;
	background-size: cover;
	background-position: 0 0;
	transition: -webkit-transform calc(700ms * 1.5) cubic-bezier(0.19, 1, 0.22, 1);
	transition: transform calc(700ms * 1.5) cubic-bezier(0.19, 1, 0.22, 1);
	transition: transform calc(700ms * 1.5) cubic-bezier(0.19, 1, 0.22, 1), -webkit-transform calc(700ms * 1.5) cubic-bezier(0.19, 1, 0.22, 1);
	transition: -webkit-transform calc(var(--d) * 1.5) var(--e);
	transition: transform calc(var(--d) * 1.5) var(--e);
	transition: transform calc(var(--d) * 1.5) var(--e), -webkit-transform calc(var(--d) * 1.5) var(--e);
	pointer-events: none;
}
.about-card:after {
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 200%;
	pointer-events: none;
	background-image: linear-gradient(
		to bottom,
		rgba(0, 0, 0, 0) 0%,
		rgba(0, 0, 0, 0.009) 11.7%,
		rgba(0, 0, 0, 0.034) 22.1%,
		rgba(0, 0, 0, 0.072) 31.2%,
		rgba(0, 0, 0, 0.123) 39.4%,
		rgba(0, 0, 0, 0.182) 46.6%,
		rgba(0, 0, 0, 0.249) 53.1%,
		rgba(0, 0, 0, 0.32) 58.9%,
		rgba(0, 0, 0, 0.394) 64.3%,
		rgba(0, 0, 0, 0.468) 69.3%,
		rgba(0, 0, 0, 0.54) 74.1%,
		rgba(0, 0, 0, 0.607) 78.8%,
		rgba(0, 0, 0, 0.668) 83.6%,
		rgba(0, 0, 0, 0.721) 88.7%,
		rgba(0, 0, 0, 0.762) 94.1%,
		rgba(0, 0, 0, 0.79) 100%
	);
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	transition: -webkit-transform calc(700ms * 2) cubic-bezier(0.19, 1, 0.22, 1);
	transition: transform calc(700ms * 2) cubic-bezier(0.19, 1, 0.22, 1);
	transition: transform calc(700ms * 2) cubic-bezier(0.19, 1, 0.22, 1), -webkit-transform calc(700ms * 2) cubic-bezier(0.19, 1, 0.22, 1);
	transition: -webkit-transform calc(var(--d) * 2) var(--e);
	transition: transform calc(var(--d) * 2) var(--e);
	transition: transform calc(var(--d) * 2) var(--e), -webkit-transform calc(var(--d) * 2) var(--e);
}
.about-card:nth-child(1):before {
	background-image: url(/react-portfolio/static/media/code.59c500d3.jpg);
}
.about-card:nth-child(2):before {
	background-image: url(/react-portfolio/static/media/education.a1304c64.jpg);
}
.about-card:nth-child(3):before {
	background-image: url(/react-portfolio/static/media/exp.ecf38ad7.jpg);
}
.about-card:nth-child(4):before {
	background-image: url(/react-portfolio/static/media/vision.e4731faf.jpg);
}

.content {
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
	padding: 1rem;
	transition: -webkit-transform 700ms cubic-bezier(0.19, 1, 0.22, 1);
	transition: transform 700ms cubic-bezier(0.19, 1, 0.22, 1);
	transition: transform 700ms cubic-bezier(0.19, 1, 0.22, 1), -webkit-transform 700ms cubic-bezier(0.19, 1, 0.22, 1);
	transition: -webkit-transform var(--d) var(--e);
	transition: transform var(--d) var(--e);
	transition: transform var(--d) var(--e), -webkit-transform var(--d) var(--e);
	z-index: 1;
}
.content > * + * {
	margin-top: 1rem;
}

.title {
	font-size: 1.3rem;
	font-weight: bold;
	line-height: 1.2;
}

.copy {
	font-family: "Cardo", serif;
	font-family: var(--font-serif);
	font-size: 1.125rem;
	font-style: italic;
	line-height: 1.35;
}

@media (hover: hover) and (min-width: 600px) {
	.about-card:after {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}

	.content {
		-webkit-transform: translateY(calc(100% - 4.5rem));
		        transform: translateY(calc(100% - 4.5rem));
	}
	.content > *:not(.title) {
		opacity: 0;
		-webkit-transform: translateY(1rem);
		        transform: translateY(1rem);
		transition: opacity 700ms cubic-bezier(0.19, 1, 0.22, 1), -webkit-transform 700ms cubic-bezier(0.19, 1, 0.22, 1);
		transition: transform 700ms cubic-bezier(0.19, 1, 0.22, 1), opacity 700ms cubic-bezier(0.19, 1, 0.22, 1);
		transition: transform 700ms cubic-bezier(0.19, 1, 0.22, 1), opacity 700ms cubic-bezier(0.19, 1, 0.22, 1), -webkit-transform 700ms cubic-bezier(0.19, 1, 0.22, 1);
		transition: opacity var(--d) var(--e), -webkit-transform var(--d) var(--e);
		transition: transform var(--d) var(--e), opacity var(--d) var(--e);
		transition: transform var(--d) var(--e), opacity var(--d) var(--e), -webkit-transform var(--d) var(--e);
	}

	.about-card:hover,
	.about-card:focus-within {
		-webkit-align-items: center;
		        align-items: center;
	}
	.about-card:hover:before,
	.about-card:focus-within:before {
		-webkit-transform: translateY(-4%);
		        transform: translateY(-4%);
	}
	.about-card:hover:after,
	.about-card:focus-within:after {
		-webkit-transform: translateY(-50%);
		        transform: translateY(-50%);
	}
	.about-card:hover .content,
	.about-card:focus-within .content {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
	.about-card:hover .content > *:not(.title),
	.about-card:focus-within .content > *:not(.title) {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
		transition-delay: calc(700ms / 8);
		transition-delay: calc(var(--d) / 8);
	}

	.about-card:focus-within:before,
	.about-card:focus-within:after,
	.about-card:focus-within .content,
	.about-card:focus-within .content > *:not(.title) {
		transition-duration: 0s;
	}
}

